.card {
  width: 22rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.card_header {
  display: flex;
  align-items: center;
  justify-content: center;
}

.card_footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
